.container{
    height: 100vh;
    padding: 5rem;
    box-sizing: border-box;
}
.vector-graphics{
    filter: drop-shadow(0 40px 10px rgba(0,0,0,0.3));
    animation: vectorAnimation 1s infinite ease-in-out alternate;
}
@keyframes vectorAnimation{
    to{
        transform:translateY(-10px);
    }
}
.brands-banner>svg{
    color: white;
    font-size: 3rem;
    transition: color 0.5s ;
}
.brands-banner>svg:hover{
    color: rgb(225,221,0);
}
.container2{
    height: 100vh;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}
.container2 video{
    box-shadow: 0 0 10px rgba(0,0,0,0.175);
    width: 60%;
    border-radius: 5px;
    outline: none;
}
@media screen and (max-width:600px){
    .container2 video{
        width: 100%;
    }
    .container2{
        height: unset;
        padding: 20vh 0;
    }
}
.course{
    width: 250px;
    margin-bottom: 1rem;
    transition: all 0.5s;
}
.course:hover{
    transform: translateY(-10px);
}